<template>
  <div
    v-loading="search.location === '' || loading"
    class="hg-search-view">
    <div class="hg-search-view__bg" />
    <div class="hg-search-view__form">
      <div class="hg-search-view__form-title">
        Search
      </div>
      <search-form
        :data="search"
        @search="onSearch" />
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, reactive, onMounted } from 'vue'
import { useRouter } from 'vue-router'
import { ElMessage } from 'element-plus'
import SearchForm from '@/components/SearchForm.vue'
import { ISearch } from '@/typing/interfaces/ISearch'
import { getCurrentCoords } from '@/utils/Geolocation'
import { DEFAULT_SEARCH_DATA, toServer } from '@/constants/search'
import { Api } from '@/utils/Api'

export default defineComponent({
  name: 'SearchView',
  components: {
    SearchForm
  },
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  setup(props: unknown, setupContext) {
    const router = useRouter()
    const loading = ref<boolean>(true)
    const searchData = reactive(DEFAULT_SEARCH_DATA)
    async function onSearch(payload: ISearch) {
      try {
        loading.value = true
        const params = toServer(payload)
        const { data } = await Api.find(params)
        loading.value = false
        if (data?.length) {
          router.push({
            name: 'MapView',
            query: {
              search: encodeURIComponent(JSON.stringify(params))
            },
            params: {
              data: JSON.stringify(data)
            }
          })
        } else {
          ElMessage({
            type: 'warning',
            message: 'No results were found for this query. Please change your search parameters.'
          })
        }
      } catch (error) {
        loading.value = false
        ElMessage({
          type: 'warning',
          message: 'Oops, something went wrong. Please contact support.'
        })
      }
    }
    onMounted(async() => {
      try {
        loading.value = true
        const coords = await getCurrentCoords()
        loading.value = false
        searchData.location = [coords.latitude, coords.longitude].join(',')
      } catch (error) {
        loading.value = false
        console.error(error)
      }
    })

    return {
      loading,
      search: searchData,
      onSearch
    }
  }
})
</script>

