<template>
  <div class="hg-search-form">
    <!-- location -->
    <div class="hg-search-form__item hg-search-form--location is-wide">
      <div class="hg-search-form__item-label">
        Location
      </div>
      <el-tooltip
        effect="dark"
        content="Sorry, we're only using the current device position for now!"
        placement="top">
        <el-select
          class="hg-search-form__item-element"
          placeholder="Current location"
          :disabled="true" />
      </el-tooltip>
    </div>
    <!-- query -->
    <div class="hg-search-form__item hg-search-form--type is-wide">
      <div class="hg-search-form__item-label">
        Activity type
      </div>
      <el-select
        v-model="value.query"
        filterable
        class="hg-search-form__item-element"
        placeholder="Select">
        <el-option
          v-for="(item, index) in types"
          :key="index"
          :label="item"
          :value="item" />
      </el-select>
    </div>
    <!-- distance -->
    <div class="hg-search-form__item hg-search-form--distance">
      <div class="hg-search-form__item-label">
        Distance (miles)
      </div>
      <el-input-number
        v-model="value.distance"
        class="hg-search-form__item-element"
        placeholder="Miles"
        size="mini"
        :step="0.5"
        :min="0"
        :max="500" />
    </div>
    <!-- duration -->
    <div class="hg-search-form__item hg-search-form--distance">
      <div class="hg-search-form__item-label">
        Duration (hours)
      </div>
      <el-input-number
        v-model="value.duration"
        class="hg-search-form__item-element"
        placeholder="Hours"
        size="mini"
        :step="0.5"
        :min="0"
        :max="48" />
    </div>
    <div class="hg-search-form__item hg-search-form__item--search">
      <el-button
        :disabled="!value || !value.distance && !value.duration || !value.query"
        type="primary"
        round
        @click="$emit('search', value)">
        Here goes!
      </el-button>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, reactive } from 'vue'
import { DEFAULT_SEARCH_DATA, SEARCH_TYPES } from '@/constants/search'
import { ISearch } from '@/typing/interfaces/ISearch'

export interface ISearchFormProps {
  data: ISearch;
}

export default defineComponent({
  name: 'SearchForm',
  emits: ['search'],

  setup(props: ISearchFormProps) {
    const value = reactive(props.data || DEFAULT_SEARCH_DATA)

    return {
      value,
      types: [...SEARCH_TYPES]
    }
  }
})
</script>

